.hero-serv {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/servicios-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
            }
        }
    }
}

//contenido
.container-items{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    .item{
        padding-top: .5rem;
        text-align: center;
        align-items: center;
        .proTitle,.price {
            font-weight: $black-f;
        }
            img{
                border-radius: .5rem;
                width: 25rem;
                height: 25rem;
            }
    
        background-color: $grayC;
        box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);
        border-radius: 1rem;
        &:hover{
            box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.5);
            transform: scale(1.1);
        }
    }
    .info-product{
        padding: 1.5rem 3rem;
        line-height: 1;
        display: flex;
        flex-direction: column;
        gap: 10px;
        .price{
            font-size: 18px;
            font-weight: 900;
        }
    }
}
// Modal Container
.modal-container {
    width: 80%;
    margin: 0 10%;
    max-height: 80vh;
    color: $white;
    background-color: $grayC;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    overflow: auto;
    z-index: 9;
    top: 8rem;
    scrollbar-color: none;
    &::-webkit-scrollbar{
        display: none;
    }
    .modal-header {
        display: flex;
        justify-content: space-around;
        background-color: $gray;
        color: $white;
        width: 100%;
        .modal-header-title{
            justify-content: start;
        }
    }
    .modal-content {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 70%;
        margin: 1rem;
        .sumar,.restar  {
            cursor: pointer;
        }
    }
    img {
        height: 12rem;
        width: 12rem;
    }
    .total-content {
        font-size: 2.5rem;
        padding: 2rem;
        text-align: center;
        padding-top: 2rem;
    }
    .delete-product {
        cursor: pointer;
    }
}
.icon-carrito {
    .cantidad-carrito {
        background-color: $white;
        color: $black;
        border-radius: 50%;
        font-size: 1.2rem;
        padding: 4px;
        top: 3.7rem;
        right: 1.4rem;
        position: absolute;
        display: none;
    }
}
@include tablet {
    .container-items {
        display: flex;
        flex-wrap: wrap;
        .item {
            width: 32rem;
            margin: 1rem;
        }
    }
}
@include telefono{
    .hero-serv {
        .color {
            .textos {
                h1 {
                font-size: 2.7rem;
                margin-left: 1rem;
                }
            }
        }
    }
}

