/** Media Queries **/

@mixin telefono {
    @media (max-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $white ;
    font-weight: $black-f;
    text-decoration: none;
    padding:1rem 1.5rem;
    text-align: center;
    display: $display;
    border: none;
    cursor: pointer;
    &:hover {
        background-color: darken($bgColor,5);
    }
}